body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}
.navbar {
  background-color: #333;
  padding: 15px 20px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
}

.navbar-search {
  display: none;
  position: absolute;
  top: 100%; /* Position below the navbar */
  width: 90%;
  background-color: white;
  padding: 10px;
  border-radius: 4px;
  transition: all 0.3s ease;
}

.navbar-search.open {
  display: flex;
}

.navbar-input {
  width: 100%;
  padding: 5px 10px;
}

.search-button {
  padding: 10px;
  background-color: #d32f2f;
  border-radius: 4px;
}

.mobile-search-button {
  display: none;
}

.navbar-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-logo img {
  max-height: 50px;
  width: auto;
}

.navbar-links {
  list-style-type: none;
  display: flex;
  gap: 20px;
}

.navbar-links li a {
  color: white;
  text-decoration: none;
  font-size: 16px;
  transition: color 0.3s ease;
}

.navbar-links li a:hover {
  color: #f4a261;
}

.menu-toggle {
  display: none;
  font-size: 24px;
  color: white;
  background: none;
  border: none;
  cursor: pointer;
}

@keyframes slide-up {
  0% {
    transform: translateY(30px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@media (max-width: 768px) {
  .navbar-search {
    display: none;
  }

  .mobile-search-button {
    display: inline-block;  /* Visible on mobile */
  }

  .navbar-content {
    flex-wrap: nowrap;
    overflow: hidden;
  }

  .navbar-links {
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: flex-end;
  }

  .navbar-links.open {
    display: flex;
  }

  .menu-toggle {
    display: block;
  }

  .navbar-links li {
    margin: 0;
  }

  .navbar-links li a {
    font-size: 18px;
    padding: 10px 0;
  }
}

