}

.scroller {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  z-index: 998;
}

.scroller__inner span {
  color: white;
  font-weight: bold;
  padding: 0 10px;
}
.scroller__inner {
  display: flex;
  padding-block: 1rem;
  gap: 2rem;
  animation: scroll var(--_animation-duration, 30s) linear infinite;
  z-index: 1000;
}
.scroller__inner a {
  text-decoration: none; /* Remove underline */
  color: white; /* Make link color white */
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 10px; /* Padding around the sponsor name */
  width: auto;
}
.scroller__inner a:hover {
  color: #ff4500; /* Hover effect (you can adjust this) */
}

.scroller[data-animated="true"] {
  overflow: hidden;
  -webkit-mask: linear-gradient(
    90deg,
    transparent,
    white 20%,
    white 80%,
    transparent
  );
  mask: linear-gradient(90deg, transparent, white 20%, white 80%, transparent);
}

.scroller[data-direction="left"] {
  --_animation-direction: forwards;
}

.scroller[data-speed="slow"] {
  --_animation-duration: 30s;
}

@keyframes scroll {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}
.scroller__inner::after {
  content: attr(data-content);
  display: flex;
